(function() {
  'use strict';

  angular
    .module('instantblog')
    .controller('MainController', MainController);

  /** @ngInject */
  function MainController($timeout, webDevTec, toastr) {
    var vm = this;

    vm.awesomeThings = [];
    vm.classAnimation = '';
    vm.creationDate = 1445280787871;
    vm.showToastr = showToastr;

    activate();

    function activate() {
      getWebDevTec();
      $timeout(function() {
        vm.classAnimation = 'rubberBand';
      }, 4000);
    }

    function showToastr() {
      toastr.info('Fork <a href="https://github.com/Swiip/generator-gulp-angular" target="_blank"><b>generator-gulp-angular</b></a>');
      vm.classAnimation = '';
    }

    function getWebDevTec() {
      vm.awesomeThings = webDevTec.getTec();

      angular.forEach(vm.awesomeThings, function(awesomeThing) {
        awesomeThing.rank = Math.random();
      });
    }
  }
})();

(function () {
  'use strict';

  angular
    .module('instantblog')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('/', {
        url: '/:instauser',
        templateUrl: function ($stateParams) {
          if ($stateParams.instauser === 'about' || $stateParams.instauser === '' || $stateParams.instauser === null) {
            return 'app/blog/about.html';
          } else {
            return 'app/blog/blog.html'
          }
        },
        controller: 'BlogController',
        controllerAs: 'blog',
        resolve: {
          CurrentInstaUser: function (InstagramApiServices, $stateParams) {
            var username = $stateParams.instauser;
            var currentInstaUser = {};
            currentInstaUser.usernameSearched = username;
            if (username === 'about') {
              return currentInstaUser;
            } else {
              return InstagramApiServices.getUserId(username)
                .then(function (response) {
                  //$log.log('response', response);
                  if (!response.data || response.data.length < 1) {
                    currentInstaUser.errorMessage = 'user ( ' +username +' ) not found';
                    return currentInstaUser;
                  } else {
                    var matchedUser;
                    for (var i = 0; i < response.data.length; i++) {
                      if (response.data[i].username === username) {
                        matchedUser = response.data[i];
                        break;
                      }
                    }
                    currentInstaUser.currentUser = matchedUser;
                    return getuserDate(matchedUser);
                  }
                });
            }

            function getuserDate(matchedUser) {
              if (!matchedUser) {
                currentInstaUser.errorMessage = 'user not found';
                return currentInstaUser;
              } else {
                return InstagramApiServices.getUser(matchedUser.id).then(function (response) {
                  currentInstaUser.currentUser = response.data;
                  if (!response.data) {
                    //$log.log('userData is', response);
                    currentInstaUser.errorMessage = response.meta.error_message;
                    currentInstaUser.isPrivate = true;
                    return currentInstaUser;
                  } else {
                    return InstagramApiServices.getUserRecentMedia(response.data.id).then(function (response) {
                      currentInstaUser.nextUrl = response.pagination.next_url;
                      currentInstaUser.currentMedia = response.data;
                      currentInstaUser.errorMessage = '';
                      currentInstaUser.isPrivate = false;
                      //$log.log(currentInstaUser.nextUrl);
                      //$log.log(currentInstaUser);
                      return currentInstaUser;
                    });
                  }
                });
              }
            }
          }
        }
      });

    $urlRouterProvider.otherwise('/about');
  }

})();

(function () {
  'use strict';

  angular
    .module('instantblog')
    .controller('BlogController', BlogController);

  /** @ngInject */
  function BlogController(InstagramApiServices, CurrentInstaUser, FireBaseService, $state) {
    var vm = this;

    vm.timelineView = true;

    vm.CurrentInstaUser = CurrentInstaUser;
    vm.currentUser = CurrentInstaUser.currentUser || {};
    vm.currentMedia = CurrentInstaUser.currentMedia ||{};
    vm.nextUrl = CurrentInstaUser.nextUrl || null;
    vm.FireBaseService = FireBaseService;

    vm.loadMore = function () {
      InstagramApiServices.loadMore(vm.nextUrl).then(function (response) {
        Array.prototype.push.apply(vm.currentMedia, response.data);
        vm.nextUrl = response.pagination.next_url;
      })
    };

    vm.searchUser = function (username) {
      if (username === '') {
        return;
      } else {
        return InstagramApiServices.getUserId(username)
          .then(function (response) {
            return response.data.slice(0, 19);
          });
      }
    };

    vm.changeUser = function (username) {
      if (angular.isUndefined(username) || username ===null) {
        return;
      } else {
        $state.go('/', {'instauser': username});
      }
    };

  }
})
();

(function () {
  'use strict';

  angular
    .module('instantblog')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log, $rootScope, FireBaseService,$state) {
    var rootScope = $rootScope;
    //$rootScope.isTouchDevice = function () {
    //  //console.log('Not Touch Device');
    //  return 'ontouchstart' in document.documentElement;
    //};

    rootScope.$on('$stateChangeStart', function () {
      rootScope.isPageLoading = true;
    });
    rootScope.$on('$stateChangeSuccess', function () {
      rootScope.isPageLoading = false;
      FireBaseService.updateFireStat($state.params.instauser);
    });
    $log.debug('runBlock end');
  }

})();

